@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    box-shadow: 0 0 10px rgba(255, 215, 0, 0.5);
  }
  50% {
    transform: scale(1.05);
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.8);
  }
}

@keyframes appear {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  50% {
    transform: scale(1.15);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-3px);
  }
}

@keyframes enemyPulse {
  0%, 100% {
    box-shadow: 0 4px 15px rgba(220, 53, 69, 0.4);
  }
  50% {
    box-shadow: 0 4px 20px rgba(220, 53, 69, 0.7);
  }
}

.grid {
  background: linear-gradient(145deg, #2d3a4f, #1e2a3a);
  padding: 12px;
  border-radius: 12px;
  box-shadow:
    0 10px 40px rgba(0, 0, 0, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
  transform-origin: center center;
}

.grid-inner {
  position: relative;
  overflow: visible;
}

@keyframes arrowColorBlink {
  0%, 100% {
    color: rgba(255, 255, 255, 0.5);
  }
  50% {
    color: #d4af37;
  }
}

.desktop-hints {
  display: block;
}

.arrow-hint {
  position: absolute;
  width: 48px;
  height: 48px;
  top: 3px;
  left: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  pointer-events: none;
}

.arrow-icon {
  font-size: 20px;
  animation: arrowColorBlink 1.5s ease-in-out infinite;
}

.move-hint {
  position: absolute;
  bottom: 0;
  right: 0;
  color: rgba(255, 255, 255, 0.5);
  font-size: 14px;
  text-align: right;
  font-weight: 600;
  white-space: nowrap;
  animation: arrowColorBlink 1.5s ease-in-out infinite;
  padding: 10px 14px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.15);
}

@keyframes playerIdleBob {
  0%, 100% {
    top: 5px;
  }
  50% {
    top: -10px;
  }
}

/* Hide desktop hints on mobile/tablet */
@media (max-width: 900px) {
  .desktop-hints,
  .move-hint {
    display: none;
  }

  .player-idle {
    animation: playerIdleBob 1s ease-in-out infinite;
  }
}

.grid-field {
  position: relative;
  width: 48px;
  height: 48px;
  background: linear-gradient(145deg, #3a4a5f, #2a3a4f);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  margin: 3px;
  box-shadow:
    inset 0 2px 4px rgba(0, 0, 0, 0.2),
    0 1px 0 rgba(255, 255, 255, 0.05);
}

.item-field {
  position: absolute;
  width: 36px;
  height: 36px;
  top: 6px;
  left: 6px;
  background: linear-gradient(145deg, #ffd700, #ffaa00);
  border-radius: 50%;
  z-index: 1;
  box-shadow:
    0 4px 15px rgba(255, 215, 0, 0.5),
    inset 0 -3px 6px rgba(0, 0, 0, 0.2),
    inset 0 3px 6px rgba(255, 255, 255, 0.4);
  animation: appear 0.3s ease-out, pulse 1.5s ease-in-out 0.3s infinite, float 2s ease-in-out infinite;
}

.item-field::before {
  content: '';
  position: absolute;
  top: 6px;
  left: 10px;
  width: 10px;
  height: 10px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 50%;
}

.enemy-field {
  position: absolute;
  width: 40px;
  height: 40px;
  top: 7px;
  left: 7px;
  border-radius: 50%;
  background: linear-gradient(145deg, #dc3545, #a71d2a);
  z-index: 2;
  box-shadow:
    0 4px 15px rgba(220, 53, 69, 0.4),
    inset 0 -3px 6px rgba(0, 0, 0, 0.3),
    inset 0 3px 6px rgba(255, 255, 255, 0.2);
  animation: enemyPulse 2s ease-in-out infinite;
  transition: transform 0.12s ease-out;
}

.enemy-field::before {
  content: '';
  position: absolute;
  top: 10px;
  left: 8px;
  width: 8px;
  height: 8px;
  background: #fff;
  border-radius: 50%;
  box-shadow: 16px 0 0 #fff;
}

.enemy-field::after {
  content: '';
  position: absolute;
  top: 12px;
  left: 10px;
  width: 4px;
  height: 4px;
  background: #1a1a2e;
  border-radius: 50%;
  box-shadow: 16px 0 0 #1a1a2e;
}

.player-field {
  position: absolute;
  width: 42px;
  height: 42px;
  top: 6px;
  left: 6px;
  border-radius: 50%;
  background: linear-gradient(145deg, #4fc3f7, #0288d1);
  z-index: 3;
  box-shadow:
    0 4px 20px rgba(79, 195, 247, 0.5),
    inset 0 -4px 8px rgba(0, 0, 0, 0.2),
    inset 0 4px 8px rgba(255, 255, 255, 0.3);
  transition: transform 0.1s ease-out;
}

.player-field::before {
  content: '';
  position: absolute;
  top: 10px;
  left: 8px;
  width: 8px;
  height: 8px;
  background: #fff;
  border-radius: 50%;
  box-shadow: 16px 0 0 #fff;
}

.player-field::after {
  content: '';
  position: absolute;
  top: 24px;
  left: 14px;
  width: 14px;
  height: 6px;
  border: 2px solid #fff;
  border-top: none;
  border-radius: 0 0 10px 10px;
}

/* Responsive grid scaling - width based */
@media (max-width: 620px) {
  .grid {
    transform: scale(0.85);
  }
}

@media (max-width: 540px) {
  .grid {
    transform: scale(0.75);
  }
}

@media (max-width: 460px) {
  .grid {
    transform: scale(0.65);
  }
}

@media (max-width: 400px) {
  .grid {
    transform: scale(0.55);
  }
}

@media (max-width: 340px) {
  .grid {
    transform: scale(0.48);
  }
}

/* Height-based scaling for landscape/small height */
@media (max-height: 750px) {
  .grid {
    transform: scale(0.8);
  }
}

@media (max-height: 650px) {
  .grid {
    transform: scale(0.7);
  }
}

@media (max-height: 550px) {
  .grid {
    transform: scale(0.6);
  }
}

/* Combined small width and height */
@media (max-width: 460px) and (max-height: 750px) {
  .grid {
    transform: scale(0.55);
  }
}

@media (max-width: 400px) and (max-height: 700px) {
  .grid {
    transform: scale(0.48);
  }
}
